import * as styleConstants from  '../constants/StyleConstants';
import Section from './Section';
import Col from './Col'
import Row from './Row'
export default () => {
    return <div id="about-us">
        <style jsx>{`
            #about-us {
                margin-bottom:300px;
            }
            .col-image {
                width:100%;
            }
        `}</style>
        <Section>
            <Row>
            <h1>FOUNDERS</h1>
                <Col first={true}>
                    <img src="/static/logger.png" className="col-image" />
                </Col>
                <Col>
                    <img src="/static/logger.png" className="col-image" />
                </Col>
                <Col last={true}>
                    <img src="/static/logger.png" className="col-image" />
                </Col>
            </Row>
                
        </Section>
    </div>
}